import React, {ReactNode, useEffect, useRef, useState} from 'react';
import styled from 'styled-components';
import closeIcon from '../../assets/images/common/loupe.svg';
import {IconButton} from '../Buttons';


type LoupeProps = {
  children: ReactNode;
};

const magnify = 2;
const loupeSize = 70;

const StyledLoupe = styled.div`
  position: relative;

  &.loupeVisible {
    cursor: none;
  }

  .loupe {
    position: absolute;
    border-radius: 50%;
    border: 1px solid black;
    overflow: hidden;
    pointer-events: none;
    transform: scale(${magnify});
    transform-origin: center;
    //opacity: 0.5;
    width: ${loupeSize}px;
    height: ${loupeSize}px;
    background-color: #ffffff;
  }

  .loupe-btn {
    position: absolute;
    right: 4.5rem;
    top: 0.55rem;
  }
`;

export const Loupe: React.FC<LoupeProps> = ({children}) => {
  const backgroundRef = useRef<HTMLDivElement | null>(null);
  const [dimensions, setDimensions] = useState({width: 0, height: 0});

  useEffect(() => {
    if (backgroundRef.current) {
      const rect = backgroundRef.current.getBoundingClientRect();
      setDimensions({
        width: rect.width,
        height: rect.height
      });
    }
  }, []);

  const [isLoupeVisible, setIsLouperVisible] = useState(false);
  const [loupePosition, setLoupePosition] = useState({x: 0, y: 0});

  const toggleLoupe = () => setIsLouperVisible(!isLoupeVisible);

  const handleMouseMove = (event: React.MouseEvent<HTMLDivElement>) => {
    if (backgroundRef.current && isLoupeVisible) {
      const {left, top} = backgroundRef.current.getBoundingClientRect();
      setLoupePosition({
        x: event.clientX - left - loupeSize,
        y: event.clientY - top - loupeSize,
      });
    }
  };

  return (
    <StyledLoupe
      ref={backgroundRef}
      onMouseMove={handleMouseMove}
      className={isLoupeVisible ? 'loupeVisible' : ''}
    >
      <IconButton
        className="loupe-btn"
        icon={closeIcon}
        onClick={toggleLoupe}
      ></IconButton>
      {children}
      {isLoupeVisible && (
        <div className='loupe'
             style={{
               left: `${loupePosition.x}px`,
               top: `${loupePosition.y}px`,
             }}
        >
          <div
            style={{
              position: 'absolute',
              left: `-${loupePosition.x}px`,
              top: `-${loupePosition.y}px`,
              width: `${magnify*460}px`,
              height: `${magnify*262}px`,
              // width: 'inherit',
              // height: 'inherit',
            }}
          >
            {children}
          </div>
        </div>
      )}
    </StyledLoupe>
  );
};
