import React, {useEffect, useState} from 'react';
import styled from 'styled-components';

import {ThemeColors} from '../../styled.d';

interface StyledButtonProps {
  color?: `${ThemeColors}`;
  size?: string;
}

const StyledButton = styled.button<StyledButtonProps>(
  ({theme, color}) => `
    position: relative;
    min-width: 6.5625rem;
    padding: 0.625rem 1rem;
    border: 0;
    border-radius: 1.125rem;
    outline: none;
    background: ${color ? theme.colors[color] : theme.colors.primary};
    background: linear-gradient(180deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);
    color: #fff;
    font-size: 0.8125rem;
    font-weight: 700;
    line-height: 1.6;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    overflow: hidden;  
    transition: background .4s ease;

    .btn-inner {
      position: relative;
      z-index: 2;
    }
    
    @keyframes rotateGradient {
0% {background: linear-gradient(180deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
10% {background: linear-gradient(170deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
20% {background: linear-gradient(160deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
30% {background: linear-gradient(150deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
40% {background: linear-gradient(140deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
50% {background: linear-gradient(130deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
60% {background: linear-gradient(120deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
70% {background: linear-gradient(110deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
80% {background: linear-gradient(100deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
90% {background: linear-gradient(90deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
100% {background: linear-gradient(80deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
    }
    
    @keyframes rotateGradientBack {
0% {background: linear-gradient(80deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
10% {background: linear-gradient(90deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
20% {background: linear-gradient(100deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
30% {background: linear-gradient(110deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
40% {background: linear-gradient(120deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
50% {background: linear-gradient(130deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
60% {background: linear-gradient(140deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
70% {background: linear-gradient(150deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
80% {background: linear-gradient(160deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
90% {background: linear-gradient(170deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
100% {background: linear-gradient(180deg, rgba(100,175,0,1) 0%, rgba(100,175,0,1) 25%, rgba(86,145,4,1) 75%, rgba(86,145,4,1) 100%);}
    }

   &.hover {
    animation: rotateGradient .4s ease forwards;
   }
   
   &.leave {
    animation: rotateGradientBack .4s ease forwards;
   }

    &:active {

    }
  `,
);

interface ButtonProps extends StyledButtonProps {
  children?: React.ReactChildren | string;
  icon?: string;
  text?: string;
  onClick?: () => void;
}

export const ButtonCSS3 = ({
                             children,
                             icon,
                             text,
                             color,
                             size,
                             onClick,
                           }: ButtonProps): React.ReactElement => {
  const [mousePosition, setMousePosition] = useState('init');

  const handleMouseEnter = () => setMousePosition('hover');
  const handleMouseLeave = () => setMousePosition('leave');

  return (
    <StyledButton
      className={mousePosition}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      color={color}
      size={size}
      onClick={onClick}>
      <span className="btn-inner">
        {!!icon && <img src={icon} />}
        {!!text && text}
        {children}
      </span>
    </StyledButton>
  );
};
