import React, {useEffect} from 'react';
import styled from 'styled-components';
import {useTranslation} from 'react-i18next';

import {Header} from '../Header';
import {Body} from '../Body';
import {Footer} from '../Footer';

import {
  useHeaderBgColorByNotificationType,
  useIconBgColorByNotificationType,
} from '../../styles/hooks';
import {
  GAActionNames,
  NavigationScreens,
  Notification,
} from '../../utils/constants';
import {
  closeWindow,
  getDeviceInfo,
  getDeviceName,
  getProductCode,
  getProductLogo,
  getProductName,
  getUpdates,
  hideWindow,
  navigateToScreen,
} from '../../services/api/actions';
import {useAnalytics} from '../../services/analytics';
import {Loupe} from '../Loupe';

interface TrayNotificationProps extends Notification {
}

export const StyledTrayNotification = styled.div`
  width: ${({theme}) => theme.notificationWidth};
`;

export const TrayNotification = ({
                                   type,
                                   bodyVerticalAlign,
                                   bodyIcon,
                                   bodyTitle,
                                   bodySubtitle,
                                   hasBodyInfo,
                                   footerMessage,
                                   footerBtnText,
                                   footerBtnGAActionName,
                                   footerBtnClickNavScreen,
                                   GAScreenName,
                                 }: TrayNotificationProps): React.ReactElement => {
  const {t} = useTranslation();
  const {sendScreenView, sendAction} = useAnalytics();

  const headerBgColor = useHeaderBgColorByNotificationType(type),
    iconBgColor = useIconBgColorByNotificationType(type),
    productCode = getProductCode(),
    productLogo = getProductLogo(productCode),
    productName = getProductName(productCode),
    deviceName = getDeviceName(),
    updates = getUpdates(),
    bodyInfo = hasBodyInfo ? getDeviceInfo() : undefined;

  const onCloseClick = () => {
    hideWindow();
    sendAction(GAActionNames.Close, GAScreenName, closeWindow);
  };

  const onOptionsClick = () => {
    sendAction(GAActionNames.Settings, GAScreenName);
    navigateToScreen(NavigationScreens.options);
  };

  const onFooterBtnClick = () => {
    sendAction(footerBtnGAActionName, GAScreenName);
    hideWindow();
    navigateToScreen(footerBtnClickNavScreen);
    closeWindow();
  };

  useEffect(() => {
    sendScreenView(GAScreenName);
  }, [sendScreenView, GAScreenName]);

  return (
    <StyledTrayNotification>
      <Loupe>
        <Header
          color={headerBgColor}
          productLogo={productLogo}
          productName={productName}
          onCloseClick={onCloseClick}
          onOptionsClick={onOptionsClick}
        />
        <Body
          color={iconBgColor}
          verticalAlign={bodyVerticalAlign}
          bodyInfo={bodyInfo}
          bodyIcon={bodyIcon}
          bodyTitle={bodyTitle && t(bodyTitle, {count: updates})}
          bodySubtitle={bodySubtitle && t(bodySubtitle, {deviceName})}
        ></Body>
        <Footer
          footerMessage={footerMessage && t(footerMessage)}
          footerBtnText={footerBtnText && t(footerBtnText)}
          onFooterBtnClick={onFooterBtnClick}
        />
      </Loupe>
    </StyledTrayNotification>
  );
};
